.auth {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    width: 396px;
    max-width: 396px;
    padding-top: 20px;
    padding-bottom: 112px;
}

.auth__logo {
    width: 38px;
    height: 38px;
    padding-top: 18px;
    cursor: pointer;
    transition: opacity ease-in-out .3s;
}

.auth__logo:hover {
    opacity: 0.3;
}

.auth__title {
    margin: 40px 0 0;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: #FFF;
    letter-spacing: 0em;
    text-align: left;
    margin-bottom: 40px;
}

.auth__form {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    position: relative;
}

.auth__fieldset {
    margin: 0;
    padding: 0;
    width: 100%;
    border: none;
}

.auth__label {
    font-family: "Inter", "sans-serif", "Arial";
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: #8B8B8B;
}

.auth__input {
    height: 41px;
    border: none;
    border-bottom: 1px solid #424242;
    background-color: #202020;
    font-family: "Inter", "sans-serif", "Arial";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    width: 100%;
}

.auth__input:focus {
    outline: none;
    border-bottom: 1px solid #4285F4;
}

.auth__input:active {
    border: none;
    background-color: #202020;
    font-family: "Inter", "sans-serif", "Arial";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
}

.auth__input:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0 50px #202020;
    -webkit-text-fill-color: #FFFFFF;
}

.auth__input:last-of-type:focus {
    color: #EE3465;
}

.auth__input::placeholder {
    background-color: transparent;
}

.auth__button {
    height: 45px;
    width: 100%;
    background-color: #4285F4;
    margin-top: 179px;
    font-family: "Inter", "sans-serif", "Arial";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    border-radius: 3px;
    transition: opacity ease-in-out .3s;
}

.auth__button:hover {
    opacity: 0.3;
}

.auth__signin {
    font-family: "Inter", "sans-serif", "Arial";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #8B8B8B;
    display: flex;
    align-items: center;
}

.auth__bottom-link {
    margin-left: 6px;
    text-decoration: none;
    color: #4285F4;
    transition: opacity ease-in-out .3s;
}

.auth__bottom-link:hover {
    opacity: 0.3;
}

@media screen and (max-width: 550px) {
    .auth {
        max-width: calc(100vw - 60px);
        align-items: center;
        padding-bottom: 30px;
    }
    .auth__signin {
        font-size: 12px;
        line-height: 15px;
    }
    .auth__button {
        margin-top: 267px;
        font-size: 12px;
        line-height: 14.5px;
    }
}