/*подключение основных стилей*/

.page {
    min-width: 320px;
    min-height: 100vh;
    font-family: "Inter", "sans-serif", "Arial";
    background-color: #202020;
    margin: 0 auto;
}
.header {
    max-width: calc(100vw - 2 * 70px);
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__logo {
    width: 38px;
    height: 38px;
    padding-top: 18px;
    cursor: pointer;
    transition: opacity ease-in-out .3s;
}

.header__logo:hover {
    opacity: 0.3;
}

@media screen and (max-width: 769px) {
    .header {
        max-width: calc(100vw - 2 * 30px);
    }
}

@media screen and (max-width: 550px) {
    .header {
        max-width: calc(100vw - 2 * 14px);
    }
}
    .navigation {
        padding-top: 21px;
    }
    
    .navigation__container {
        display: flex;
        align-items: center;
    }
    
    .navigation__container_main {
        display: flex;
        align-items: center;
    }
    
    .navigation__main-container_button {
        border: none;
        max-width: 76px;
        max-height: 32px;
        background-color: #3DDC84;
        border-radius: 3px;
        color: #000;
        font-family: "Inter", "sans-serif", "Arial";
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        padding: 8px 20px;
    }
    
    .navigation__main-container_button:hover {
        cursor: pointer;
    }
    
    .navigation__main-container_sign {
        font-family: "Inter", "sans-serif", "Arial";
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        color: #FFFFFF;
        margin-right: 30px;
        text-decoration: none;
    }
    
    .navigation__main-container_sign:hover {
        cursor: pointer;
        opacity: 0.3;
    }
    
    .navigation__main-container_sign:last-of-type {
        margin-right: 0px;
    }
    
    .navigation__burger {
        display: none;
    }
    
    .navigation__burger_active {
        display: block;
    }
    
    .navigation__burger-container {
        display: none;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        position: fixed;
        top: 0;
        right: 0;
        height: 100vh;
        width: calc(100vw - 30%);
        background-color: #272727;
        z-index: 1;
        max-height: 100vh;
    }
    
    .navigation__burger-container_active {
        display: flex;
    }
    
    .navigation__items {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .navigation__item {
        font-family: "Inter", "sans-serif", "Arial";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: #FFFFFF;
        text-decoration: none;
        margin-right: 20px;
        transition: opacity ease-in-out .3s;
        cursor: pointer;
    }
    
    .navigation__item:hover {
        opacity: 0.3;
    }
    
    .navigation__account {
        font-family: "Inter", "sans-serif", "Arial";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: #FFFFFF;
        text-decoration: none;
        margin-left: 50px;
    }
    
    .navigation__account-icon {
        margin-left: 17px;
        width: 26px;
        height: 30px;
        border-radius: 4px;
        background-color: #313131;
        background-image: url(/movies-explorer-frontend/static/media/icon__account.2301690e.svg);
        background-repeat: no-repeat;
        background-size: 11px 14px;
        background-position: center;
    }
    
    .navigation__account-container {
        display: flex;
        align-items: center;
        transition: opacity ease-in-out .3s;
    }
    
    .navigation__account-container:hover {
        opacity: 0.3;
    }
    
    .navigation__burger-btn {
        border: none;
        background-color: #202020;
    }
    
    .navigation__exit-button {
        border: none;
        background-color: #272727;
        position: absolute;
        top: 10px;
        right: 20px;
        z-index: 2;
        cursor: pointer;
    }
    
    .navigation__exit-button-icon {
        height: 22px;
        width: 22px;
    }
    
    .navigation__burger-items {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 105px;
    }
    
    .navigation__burger-item {
        margin-bottom: 28px;
        font-family: "Inter", "sans-serif", "Arial";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: #FFFFFF;
        text-decoration: none;
        margin-right: 20px;
        transition: opacity ease-in-out .3s;
        cursor: pointer;
    }
    
    .navigation__burger-item:hover {
        text-decoration: underline;
        -webkit-text-decoration-color: #FFF;
                text-decoration-color: #FFF;
        text-underline-offset: 7px;
        opacity: 0.3;
        cursor: pointer;
    }
    
    .navigation__burger-account {
        font-family: "Inter", "sans-serif", "Arial";
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        color: #FFFFFF;
        text-decoration: none;
    }
    
    .navigation__burger-account-container {
        margin-bottom: 92px;
        display: flex;
        align-items: center;
    }
    
    .navigation__burger-account_icon {
        width: 26px;
        height: 30px;
        border-radius: 4px;
    }
    
    .navigation__burger_icon {
        display: none;
    }
    
    .navigation__burger_icon:hover {
        cursor: pointer;
    }
    
    @media screen and (max-width: 796px) {
        .navigation__burger_icon {
            display: block;
        }
        .navigation__container {
            display: none;
        }
