    font-family: "Inter", "sans-serif", "Arial";
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    text-align: right;
    color: #8B8B8B;
}

.movie-card__image {
    max-width: 100%;
}

.movie-card__button {
    display: block;
    width: 100px;
    height: 29px;
    border: none;
    border-radius: 30px;
    background: #313131;
    margin: 18px auto;
    font-family: "Inter", "sans-serif", "Arial";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 11px;
    text-align: center;
    color: #FFFFFF;
    cursor: pointer;
    transition: opacity ease-in-out .3s;
}

.movie-card__button:hover {
    opacity: 0.3;
}

.movie-card__button_saved-ok {
    background: #EE3465;
}

@media screen and (max-width: 550px) {
    .movie-card__info {
        padding: 14px;
    }
    .movie-card__title {
        font-size: 12px;
    }
    .movie-card__duration {
        font-size: 10px;
    }
}

